import Box from '@mui/material/Box';
import {
    Button,
    Container,
    Grid,
    List,
    ListItem,
    ListItemButton,
    ListItemText,
    TextField,
    Pagination,
    Paper
} from "@mui/material";
import APIEndpoints from "./APIEndpoints";
import {useEffect, useState} from "react";
import debug from 'debug';

const Home = () => {
    const log = debug('app:home');

    const [url, setUrl] = useState("");
    const [alias, setAlias] = useState("");
    const [aliasData, setAliasData] = useState([]);
    const [reRun, setReRun] = useState(false);
    const [searchTerm, setSearchTerm] = useState("");
    const [currentPage, setCurrentPage] = useState(1);
    const itemsPerPage = 8;

    const newAlias = (url, alias) => {
        log("Creating new alias", {url, alias});

        fetch(APIEndpoints.new, {
            method: "POST",
            headers: {
                "Content-Type": "application/json",
            },
            body: JSON.stringify({url, alias})
        })
            .then(response => response.json())
            .then(data => {
                log("New alias created successfully", data);
                setReRun(prev => !prev);
            })
            .catch(error => {
                log("Error creating new alias", error);
            });
    };

    const getAliases = () => {
        log("Fetching aliases");

        fetch(APIEndpoints.get.all)
            .then(response => response.json())
            .then(data => {
                log("Aliases fetched successfully", data);
                setAliasData(data.data.reverse());
            })
            .catch(error => {
                log("Error fetching aliases", error);
            });
    };

    useEffect(() => {
        getAliases();
    }, [reRun]);

    // Filter aliases based on search term
    const filteredData = aliasData.filter(aliasObj =>
        aliasObj.url.toLowerCase().includes(searchTerm.toLowerCase()) ||
        aliasObj.short_alias.toLowerCase().includes(searchTerm.toLowerCase())
    );

    // Calculate pagination
    const totalPages = Math.ceil(filteredData.length / itemsPerPage);
    const displayedData = filteredData.slice(
        (currentPage - 1) * itemsPerPage,
        currentPage * itemsPerPage
    );

    const handlePageChange = (event, value) => {
        setCurrentPage(value);
    };

    const handleNewAliasCreate = () => {
        newAlias(url, alias);
        setAlias("");
        setUrl("");
    }

    return (
        <Container maxWidth="md" sx={{p: 3}}>
            <Grid container spacing={2}>
                <Grid item xs={8}>
                    <TextField
                        label="Long Url"
                        variant="outlined"
                        fullWidth
                        type="url"
                        onChange={(e) => setUrl(e.target.value)}
                    />
                </Grid>
                <Grid item xs={2}>
                    <TextField
                        label="Alias"
                        variant="outlined"
                        fullWidth
                        onChange={(e) => setAlias(e.target.value)}
                    />
                </Grid>
                <Grid item xs={2} sx={{display: "flex", justifyContent: "center"}}>
                    <Button
                        variant="contained"
                        fullWidth
                        onClick={() => handleNewAliasCreate()}
                    >
                        Shorten
                    </Button>
                </Grid>
                <Grid item xs={12}>
                    <TextField
                        label="Search"
                        variant="outlined"
                        fullWidth
                        onChange={(e) => setSearchTerm(e.target.value)}
                    />
                </Grid>
            </Grid>
            <List sx={{width: '100%', maxWidth: "100%"}}>
                {displayedData.map(aliasObj => (
                    <Paper sx={{m: 1}}>
                        <ListItem key={aliasObj._id}>
                            <ListItemButton>
                                <ListItemText
                                    primary={<Button variant={"contained"}
                                                     onClick={() => window.open(aliasObj.url, "_blank")}>{aliasObj.short_alias}</Button>}
                                    secondary={
                                        <Box sx={{display: "flex", flexDirection: "column", p: 1}}>
                                            <Box>{aliasObj.url}</Box>
                                            {/*<Box>{new Date(aliasObj.created_at).toLocaleString()}</Box>*/}
                                        </Box>
                                    }
                                />
                            </ListItemButton>

                        </ListItem>
                    </Paper>
                ))}
            </List>
            <Pagination
                count={totalPages}
                page={currentPage}
                onChange={handlePageChange}
                sx={{display: 'flex', justifyContent: 'center', mt: 2}}
            />
        </Container>
    );
};

export default Home;
